.app__work{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #e4e4e4;
}
.app__profile-image{
    position: relative;
}
.app__work-portfolio{
    padding: 5rem;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    @media screen and (max-width:1600px) {
        padding: 1rem;
    }
    .app__profile-item{
        margin: 2rem;
        width: 400px;
        @media screen and (max-width:600px) {
            width: 250px;
        }
    }
    img{
        z-index: 5;
        width: 400px;
        height: 300px;
        border-radius: 15px;
        object-fit: cover;
        background-color: #cfcfd0;
        @media screen and (max-width:600px) {
            width: 250px;
            height: 200px;
        }
    }
    h2{
        font-size: 20px;
    }
    p{
        font-size: 14px;
        text-align: justify;
    }
}
.app__portfolio-info{
    z-index: 3;
    display: flex;
    width: 250px;
    height: 200px;
    border-radius: 15px;
    justify-content: space-evenly;
    align-items: center;
}
.app__work-hover{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    opacity: 0;
    transition: all 0.3s ease;
    border-radius: 15px;
    display: flex;
    div{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,0.5);
        color: white;
        margin: 1rem;
    }
}
