.app__header{
    min-height: 100vh;
    padding: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: #e4e4e4;
    @media screen and (max-width:900px){
        flex-direction: column;
        padding: 1rem;
    }
}
.main_img{
    height: 500px;
    width: 500px;
    border-radius: 50%;
    @media screen and (max-width:1500px) {
        height: 300px;
        width: 300px;
    }
}
.tag-cmp{
    margin-left: 20px;
}
.badge-cmp{
    .p-text{
        display: inline;
    }
    h1{
        display: inline;
    }
}
.app__header-badge{
    background-color: white;
    padding: 1rem 2rem;
    border-radius: 20px;
    @media screen and (max-width:400px) {
        padding: 1rem 1rem;
    }
}