.app__skills{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.app__skills-container{
    display: flex;
    padding-top: 5rem;
    @media screen and (max-width:1220px) {
        max-width: 800px;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}
.app__skills-list{
    padding: 0rem 12rem;
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    :hover{
        font-weight: bold;
    }
    @media screen and (max-width:1850px) {
        padding: 0rem 5rem;
    }
    @media screen and (max-width:1220px) {
        width: 100%;
    }
    @media screen and (max-width:500px) {
        padding: 0rem 2rem;
    }
    
}
.app__skills-list_skill-image{
    background-color: white;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
        width: 50%;
        height: 50%;
        object-fit: cover;
    }
    &:hover{
        box-shadow: 0 0 25px #fef4f5;
    }
    @media screen and (max-width:1400px) {
        width: 80px;
        height: 80px;
    }
    @media screen and (max-width:600px) {
        width: 50px;
        height: 50px;
    }
}
.app__skills-list_skill{
    margin: 1rem;
    p{
        font-size: 14px;
        margin-top: 1rem;
        text-align: center;
        @media screen and (max-width:500px) {
            font-size: 10px;
        }
    }
}

.app__skills-experience{
    width: 50%;
    @media screen and (max-width:1220px) {
        width: 100%;
        padding: 0rem 5rem;
    }
    @media screen and (max-width:600px) {
        padding: 0rem 1rem;
    }
}


.app__skills-experience{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.app__skills-experience_container{
    margin: 5rem 1rem;
    display: flex;
    >p{
        color: var(--secondary-color);
        font-weight: 800;
        font-size: 25px;
        padding-right: 5rem;
        @media screen and (max-width:1200px) {
            padding-right: 2rem;
        }
    }
    @media screen and (max-width:650px) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 2rem 1rem;
    }
}
.app__skills-experience_container-info{
    cursor: pointer;
    h4,p{
        color: var(--gray-color);
    }
    h4{
        font-size: 25px;
        margin-bottom: 1rem;
    }
    p{
        font-size: 20px;
    }
}
.skills-tooltip{
    max-width: 300px !important;
    background-color: var(--white-color) !important;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.1) !important;
    border-radius: 5px !important;
    padding: 1rem !important;
    color: var(--gray-color) !important;
    text-align: center !important;
    line-height: 1.5 !important;
    opacity: 1 !important;
  
    @media screen and (min-width: 2000px) {
      font-size: 1.75rem !important;
      max-width: 500px !important;
      line-height: 2 !important;
    }
}