.app__contact{
    padding: 5rem 10rem;
    min-height: 100vh;
    background-color: white;
    display: flex;
    flex-direction: column;
    @media screen and (max-width:1600px) {
        padding: 1rem 5rem;
    }
    @media screen and (max-width:700px) {
        padding: 1rem 1rem;
    }
}
.app__contact-main{
    margin-top: 1rem;
    display: flex;
    justify-content:center;
    height: 100px;
    @media screen and (max-width:1200px){
        height: unset;
        flex-direction: column;
        align-items: center;
    }
}
.app__contact-main_email{
    margin: 1rem 1rem;
    border-radius: 20px;
    width: 400px;
    height: 100px;
    background-color: var(--lightGray-color);
    padding: 1rem;
    display: flex;
    align-items: center;
    @media screen and (max-width:550px){
        width: 300px;
    }
    img{
        height: 80%;
        padding-right: 1rem;
    }
    a{
        text-decoration: none;
        color: unset;
    }
}
.app__contact-main_phone{
    margin: 1rem 2rem;
    border-radius: 20px;
    padding: 1rem;
    width: 400px;
    height: 100px;
    display: flex;
    background-color: var(--primary-color);
    align-items: center;
    @media screen and (max-width:550px){
        width: 300px;
    }
    img{
        height: 80%;
        padding-right: 2rem;
    }
    a{
        text-decoration: none;
        color: unset;
    }
}
.app__contact-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 0rem;
    div{
        width: 100%;
        padding: 1rem;
    }
    input,textarea{
        background-color: var(--primary-color);
        border-radius: 10px;
        border: none;
        font-size: 16px;
        padding: 1rem;
        font-family: var(--font-base);
        &:focus{
            outline: none;
        }
    }

    input{
        width: 100%;
    }
    textarea{
        width: 100%;
        height: 300px;
        
    }
    button{
        font-size: 20px;
        text-align: center;
        padding: 1rem 1rem;
        border-radius: 10px;
        border: none;
        cursor: pointer;
        background-color: var(--secondary-color);
        color: white;
    }
}