.app__navbar{
    // padding: 0rem 20rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 2000px;
    height: 59px;
    background-color: white;
    top: 0px;
    z-index: 999;
    @media screen and (max-width:2000px) {
        width: 100%;
    }
}
.app__navbar-name{
    padding-left: 1rem ;
    h1{
        font-size: 30px;
    }
}
.app__navbar-links{
    display: flex;
    li{
        cursor: pointer;
        list-style: none;
        padding: 1rem;
        a{
            width: 100%;
            height: 100%;
            all: unset;
            font-size: 1.25rem;
        }
    }
    li:hover{
        background-color: var(--lightGray-color);
    }
    @media screen and (max-width:620px) {
        display: none;
    }
}
.app__navbar-git{
    display: flex;
    a{
        all: unset;
        cursor: pointer;
        background-color: var(--secondary-color);
        padding: 0.5rem;
        margin-right: 1rem;
        border-radius: 5px;
        color: white;
    }
    .github{
        background-color: black;
        color: white;  
    }
    @media screen and (max-width:900px) {
        display: none;
    }
}
.app__navbar-toggle{
    position: relative;
}
.app__navbar-menu_container{
    width: 200px;
    background-color: white;
    position: absolute;
    right: 2rem;
    padding: 1rem;
    border-radius: 10px;
}
.app__navbar-menu{
    display: none;
    @media screen and (max-width:900px) {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
}
.app__navbar-menu_container-links{
    display: none;
    @media screen and (max-width:620px) {
        display: flex;
        flex-direction: column;
    }
}
.app__navbar-menu_container-git{
    display: none;
    @media screen and (max-width:900px) {
        display: flex;
        flex-direction: column;
    }
}
.app__navbar-menu_container-git{
    a{
        margin: 0.5rem;
        text-align: center;
    }
}
.app__navbar-menu_container-links{
    li{
        padding: 0.5rem;
    }
}