.app__about{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.app__profiles{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    .app__profile-item{
        margin: 2rem;
        width: 300px;
    }
    img{
        width: 300px;
        height: 250px;
        border-radius: 15px;
        object-fit: cover;
        background-color: #cfcfd0;
    }
    h2{
        font-size: 20px;
    }
    p{
        padding-right: 0.5rem;
        font-size: 14px;
        text-align: justify;
    }
}
